@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
/** Media Queries **/
header {
  width: 100vw;
  position: fixed;
  overflow: hidden;
}

header .container {
  padding: 2.5rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1025px) {
  header .container {
    justify-content: space-evenly;
    align-items: center;
  }
}

.navbar-logo {
  animation: navbar-logo 2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
.navbar-logo h4 {
  font-family: "Bebas Neue", cursive;
  font-size: 6rem;
  color: #2B4F8D;
}
.navbar-logo h4 span {
  color: #02C9AF;
}

@keyframes navbar-logo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.navbar-menu {
  animation: navbar-menu 2s cubic-bezier(1, 0.6, 0.6, 0.5);
  animation-fill-mode: backwards;
  animation-delay: 1s;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navbar-menu a {
  color: #FFFFFF;
  padding: 0rem 2rem;
  font-size: 1.7rem;
}
@media (min-width: 1025px) {
  .navbar-menu {
    flex-direction: row;
  }
  .navbar-menu a {
    margin: 0 1rem;
  }
  .navbar-menu a:hover {
    animation: forwards 0.5s;
    box-shadow: 0rem 0.4rem 0.3rem -0.3rem #02C9AF;
  }
}

@keyframes navbar-menu {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}